import { useNavigate } from "react-router-dom";
import { useAuthState } from "../../context/authentication";
import SpecialItem from "./SpecialItem/SpecialItem";
import classes from "./Whyus.module.css";

const Whyus = () => {
  const authState = useAuthState();

  const bockAppointment =
    "Khng cn mt nhiu thi gian  tm kim a ch phng khm th cng t nhiu ngun khc nhau v  c DaPet  xut v nh gi.";

  const navigate = useNavigate();

  const reciveOrder =
    "n khm s c gi ngay n phng khm v c ln lch hn cho ngi dng mt cch nhanh chng";

  const petManager =
    "Th cng s c lp h s ring  ngi dng thun tin theo di";

  const gotoBookHandler = () => {
    navigate("/book");
  };

  const gotoOrderHandler = () => {
    if (!authState.state) {
      alert("Bn cn ng nhp  dng tnh nng ny!");
      return;
    } else {
      navigate("/order");
    }
  };

  const gotoPetHandler = () => {
    if (!authState.state) {
      alert("Bn cn ng nhp  dng tnh nng ny!");
      return;
    } else {
      navigate("/pet");
    }
  };

  return (
    <section className={classes["why-us"]}>
      <div className="container">
        <div className={classes["section-title"]}>
          <h2>Why Us</h2>
          <p>V sao nn s dng DaPet</p>
        </div>

        <div className="row">
          <SpecialItem
            number={1}
            title="t Lch Khm"
            text={bockAppointment}
            onClick={gotoBookHandler}
          />
          <SpecialItem
            number={2}
            title="Nhn n Khm"
            text={reciveOrder}
            onClick={gotoOrderHandler}
          />
          <SpecialItem
            number={3}
            title="Qun L Th Cng"
            text={petManager}
            onClick={gotoPetHandler}
          />
        </div>
      </div>
    </section>
  );
};

export default Whyus;
