const MedicalCard = () => {
  const navigate = useNavigate();

  const gotoOrderDetailHandler = (value) => {
    navigate(`/order/${value}`);
  };

  return (
    <section className={classes["container"]}>
      {medicalApp.map((item, index) => {
        return (
          <MedicalCardItem
            key={index}
            item={item}
            onClick={gotoOrderDetailHandler}
          />
        );
      })}
    </section>
  );
};

export default MedicalCard;
