// import classes from "./PetInfo.module.css";

import React from "react";

const PetInfo = ({ dataPet }) => {
  return (
    <div className="py-2 px-1 rounded d-flex flex-column gap-2 mt-2">
      <div className="fs-5 fw-bolder">Thng tin vt nui</div>
      {dataPet && (
        <React.Fragment>
          <div>
            Vt nui: <span className="fw-bold">{dataPet.type}</span>
          </div>
          <div>
            Tn: <span className="fw-bold">{dataPet.name}</span>
          </div>
          <div>
            Triu chng:{" "}
            <span className="fw-bold">
              {dataPet.description.reduce(
                (result, current) => result.value + ", " + current.value
              )}
            </span>
          </div>
          <div>
            <div>Ch thch</div>
            <div className="fw-bold">{dataPet.comment}</div>
          </div>
        </React.Fragment>
      )}
    </div>
  );
};

export default PetInfo;
