// import classes from "./Description.module.css";
import DescriptionItem from "../DescriptionItem/DescriptionItem";

const Description = ({ info }) => {
  const getSymptom = () => {
    const length = info.symptom.length;
    if (length === 1) {
      return info.symptom[length - 1][0].value;
    }
    if (info.symptom) {
      return info.symptom[length - 1].reduce((result, current) => {
        return result.value + ", " + current.value;
      });
    }
  };
  return (
    <div className="bg-white p-3 rounded flex-fill">
      {info.type && (
        <DescriptionItem title="Vt nui" description={info.type} />
      )}
      {info.symptom && (
        <DescriptionItem title="Triu chng">{getSymptom()}</DescriptionItem>
      )}
      <DescriptionItem title="Phng khm" description={info.clinic.name} />
      <DescriptionItem title="a ch" description={info.clinic.address} />
      <DescriptionItem
        title="Thi gian"
        description={info.clinic.date + " at " + info.clinic.timeline}
      />
      {info.other && (
        <DescriptionItem title="Ch thch" description={info.other} />
      )}
    </div>
  );
};

export default Description;
