import classes from "./Menu.module.css";
import React from "react";
import {
  BsPerson,
  BsPencil,
  BsShopWindow,
  BsQuestionSquare,
} from "react-icons/bs";
import Account from "../Account/Account";
import MenuItem from "./MenuItem/MenuItem";
import { useNavigate } from "react-router-dom";
import { useAuthState } from "../../context/authentication";

function Menu() {
  const authState = useAuthState();
  const navigate = useNavigate();

  const gotoPageNotFoundHandler = () => {
    navigate("/page-not-found");
  };

  return (
    <div className={`${classes["container-menu"]} bg-white p-3 rounded`}>
      <MenuItem
        icon={<BsPerson />}
        title="Thng tin c nhn"
        onClick={gotoPageNotFoundHandler}
      />
      <MenuItem
        icon={<BsPencil color="#7D5A50" />}
        title="ng k khm"
        selected="selected"
        onClick={gotoPageNotFoundHandler}
      />
      <MenuItem
        icon={<BsShopWindow />}
        title="Tm kim c s khm"
        onClick={gotoPageNotFoundHandler}
      />
      <MenuItem
        icon={<BsQuestionSquare />}
        title="Hi p"
        onClick={gotoPageNotFoundHandler}
      />
      {authState.state && <Account />}
    </div>
  );
}

Menu.propTypes = {};

export default Menu;
